.packsList {
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  overflow: auto;
  margin-bottom: 28px;

  .packsHeader {
    display: flex;
    align-items: center;
    height: 48px;
    font-weight: 700;
    background-color: #F8F7FD;
    padding: 0 15px;
    justify-content: space-between;

    .name {
      width: 120px;
    }

    .cardsCount {
      width: 80px;
      text-align: center;
    }

    .updated {
      width: 110px;
      text-align: center;
    }

    .created {
      width: 140px;
      text-align: center;
    }

    .actions {
      width: 146px;
      text-align: center;
    }
  }

  .packs {

  }

}

