.profilePage {
  background: linear-gradient(to bottom, #E3D3E0, #AFA4CD);
  padding-top: 30px;
  height: 100vh;

  .container {
    display: flex;
    width: 1008px;
    margin: 0 auto;
    border-radius: 8px;
    background-color: #FEFEFF;

    .leftPart {
      min-width: 243px;
      background-color: #ECECF9;
      border-radius: 8px 0 0 8px;

      .profileBlock {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 243px;
        background-color: #D9D9F1;
        padding: 24px 24px 0 24px;
        text-align: center;
        border-radius: 8px 0 0 0;

        .img {
          width: 96px;
          height: 96px;
          margin-bottom: 10px;
        }

        .title {
          margin-bottom: 20px;
        }

      }

      .rangeBlock {
        padding: 24px 24px 0 24px;

        .rangeTitle {
          margin-bottom: 50px;
        }
      }
    }

    .rightPart {
      h2 {
        margin-bottom: 15px;
      }

      width: 100%;
      padding: 24px 30px 10px 30px;

      .block {
        display: flex;
        margin-bottom: 24px;
        justify-content: space-between;

        .searchBlock {
          display: flex;
          justify-content: space-between;
          width: 65%;


          .searchInp {
            width: 100%;
            margin-right: 20px;
            background: #ECECF9;
            border: 1px solid #635D80;
            height: 36px;
            padding-left: 10px;
            opacity: 0.5;
            border-radius: 2px;
          }

          .searchBtn {
            width: 150px;
            height: 36px;
            background: #D7D8EF;
            border-radius: 30px;
            color: #21268F;
            cursor: pointer;
            border: none;

            &:hover {
              background: rgba(220, 220, 240, 1);
              border: 1px solid;
            }
          }
        }

        .addPackBtn {
          width: 184px;
          height: 36px;
          background: #21268F;
          box-shadow: 0px 4px 18px rgba(33, 38, 143, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
          border-radius: 30px;
          color: white;
          cursor: pointer;
          border: none;

          &:hover {
            border: 1px solid;
          }
        }

      }

      .pagSelectBlock {
        display: flex;

        .selectWrapper {
          margin-left: 10px;
          display: flex;
          align-items: center;

          span {
            margin-right: 10px;
          }
        }
      }

      .notPacks {
        text-align: center;
        margin-top: 150px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        opacity: 0.5;
      }
    }
  }
}
